<template>
    <div
        class="neck"
    >
        <String
            v-for="(string, i) in stringOrder"
            :key="i"
            :class="i === 0 ? 'first' : i === stringOrder.length - 1 ? 'last': ''"

            :string="string"
            :length="length"
            :selected="selectedNotes"
            :show-notes="showNotes"
            :marks="marks"
            @select="noteSelected"
        />
    </div>
</template>

<script>
    import String from "@/components/String";

    export default {
        name: "Neck",
        components: {String},
        props: {
            strings: {
                type: Array,
                default: () => ['E', 'A', 'D', 'G', 'B', 'E']
            },
            length: {
                type: Number,
                default: 21
            },
            showNotes: {
                type: Boolean,
                default: false
            },
            marks: {
                type: Array,
                default: () => []
            }
        },
        data() {
            return {
                selectedNotes: []
            }
        },
        computed: {
            stringOrder() {
                return this.strings;
            }
        },
        methods: {
            noteSelected(note) {
                if (this.selectedNotes.indexOf(note) > -1)
                    this.selectedNotes = this.selectedNotes.filter(v => v !== note);
                else
                    this.selectedNotes.push(note);
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../assets/settings";

    .neck {
        margin: 0 auto;
        padding: 20px;
        background: $primary;
        @include clearfix();
    }
</style>