//if You have build issues in the future, there is something going on with the public path in the vue.config.js file

import Vue from 'vue'
import App from './App.vue'
import VueMaterial from 'vue-material'
import 'vue-material/dist/vue-material.min.css'

Vue.config.productionTip = false

Vue.mixin({
    methods: {
        isMobile() {
            return /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
        }
    }
});

Vue.use(VueMaterial)

new Vue({
    render: h => h(App),
}).$mount('#app')
