<template>
    <div
        :class="['string', showNotes ? 'show-notes' : '']"
    >
        <div
            v-for="(note, i) in notes"
            :key="i"
            :class="[
            'note',
            selected.indexOf(note) > -1 ? 'selected': '',
            marks.indexOf(i) > -1 ? 'fret-marker' : '',
            i === 12 ? 'double-fret' : '',
            i === 0 ? 'string-descriptor' : ''
            ]"
            @click="select(note)"
        >
            {{ note }}
        </div>
    </div>
</template>

<script>

    let notes = ['C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#', 'A', 'A#', 'B'];

    export default {
        name: "String",
        props: {
            string: {
                type: String,
                required: true
            },
            length: {
                type: Number,
                default: 21
            },
            selected: {
                type: Array,
                default: () => []
            },
            marks: {
                type: Array,
                default: () => [3, 5, 7, 9, 12, 15, 17, 19]
            },
            showNotes: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            notes() {
                let rtv = [];

                let n = notes.indexOf(this.string);
                if (n >= notes.length)
                    n = 0;

                for (let i = 0; i < this.length; i++) {
                    rtv.push(notes[n]);

                    n++;
                    if (n >= notes.length)
                        n = 0;
                }
                return rtv;
            }
        },
        methods: {
            select(note) {
                this.$emit('select', note);
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../assets/settings";

    .string {
        clear: both;
        position: relative;
        @include clearfix();

        .fret-marker {
            position: relative;

            &:before, &:after {
                position: absolute;
                width: 10px;
                height: 10px;
                background: white;
                border-radius: 50%;
            }
        }

    }

    .show-notes {
        .note {
            color: black;
        }
    }

    .note {
        float: left;
        width: 50px;
        height: 50px;
        line-height: 50px;
        border-top: solid 1px $fret-color;
        text-align: center;
        cursor: pointer;
        transition: .3s;
        color: white;
        background: white;

        + .note {
            border-left: solid 1px $fret-color;
        }

        $interact: lighten($fret-color, 3%);

        &.selected {
            background: $interact;
            color: $primary;
            font-weight: bold;
            font-size: 16px;
        }

        &:hover {
            background: $interact;
            transition: .3s;
            color: black;
        }

        &.string-descriptor {
            background: lighten($fret-color, 2%);
            color: black;

            &.selected {
                background: lighten($primary, 40%);
                color: black;
            }
        }
    }
</style>