<template>
    <div
        id="app"
        :class="[horizontal ? 'horizontal' : 'vertical']"
    >
        <md-app style="min-height: 100vh;" md-mode="reveal">
            <md-app-toolbar class="md-primary">
                <md-button class="md-icon-button" @click="toggleMenu" v-if="!menuVisible">
                    <md-icon>menu</md-icon>
                </md-button>
                <span class="md-title" style="flex: 1">Fretboard</span>
                <md-menu md-align-trigger :mdCloseOnClick="true" :mdCloseOnSelect="true">
                    <md-button md-menu-trigger>Instrument</md-button>
                    <md-menu-content>
                        <md-menu-item @click="setGuitar">Guitar</md-menu-item>
                        <md-menu-item @click="setBass">Bass</md-menu-item>
                        <md-menu-item @click="set5sBass">5 String Bass - Low B</md-menu-item>
                        <md-menu-item @click="set5sBassAlt">5 String Bass - High C</md-menu-item>
                        <md-menu-item @click="setUkulele">Ukulele</md-menu-item>
                        <md-menu-item @click="setMandolin">Mandolin</md-menu-item>
                    </md-menu-content>
                </md-menu>
            </md-app-toolbar>

            <md-app-drawer
                :md-active.sync="menuVisible"
            >
                <md-toolbar class="md-transparent" md-elevation="0">
                    <span>Menu</span>
                    <div class="md-toolbar-section-end">
                        <md-button class="md-icon-button md-dense" @click="toggleMenu">
                            <md-icon>keyboard_arrow_left</md-icon>
                        </md-button>
                    </div>
                </md-toolbar>
                <md-divider></md-divider>
                <md-list>
                    <md-subheader>View Options</md-subheader>
                    <md-list-item v-if="!isMobile()">
                        <md-switch v-model="horizontal" class="md-primary">Horizontal</md-switch>
                    </md-list-item>
                    <md-list-item>
                        <md-switch v-model="showNotes" class="md-primary">Show All Notes</md-switch>
                    </md-list-item>
                    <md-divider></md-divider>
                    <md-subheader>Instrument Options</md-subheader>
                    <md-list-item>
                        <md-field>
                            <label>Strings (Comma Separated)</label>
                            <md-input v-model="strings" class="md-primary"></md-input>
                        </md-field>
                    </md-list-item>
                    <md-list-item>
                        <md-field>
                            <label>Fretboard Length</label>
                            <md-input v-model="length" class="md-primary"></md-input>
                        </md-field>
                    </md-list-item>
                    <md-list-item>
                        <md-field>
                            <label>Marks On</label>
                            <md-input v-model="marks" class="md-primary"></md-input>
                        </md-field>
                    </md-list-item>
                    <md-divider></md-divider>
                </md-list>
            </md-app-drawer>
            <md-app-content>
                <div
                    v-if="selectedStrings.length > 0"
                    class="neck-container"
                >
                    <Neck
                        :strings="selectedStrings"
                        :length="length"
                        :show-notes="showNotes"
                        :marks="convertedMarks"
                    />
                </div>
                <md-empty-state
                    v-else
                    md-icon="error_outline"
                    md-label="No Strings Selected"
                    md-description="You have not specified any strings! Please select an instrument or create a custom string configuration."
                >
                    <md-button
                        @click="setGuitar"
                        class="md-raised md-primary"
                    >
                        Set To Guitar
                    </md-button>
                </md-empty-state>
            </md-app-content>
        </md-app>
    </div>
</template>

<script>
    import Neck from "@/components/Neck";

    export default {
        name: 'app',
        components: {
            Neck
        },
        data() {
            return {
                menuVisible: false,
                showNotes: false,
                horizontal: !this.isMobile(),
                length: 21,
                strings: ['E', 'A', 'D', 'G', 'B', 'E'].join(", "),
                marks: [3, 5, 7, 9, 12, 15, 17, 19].join(", ")
            }
        },
        computed: {
            selectedStrings() {
                if (this.strings === "")
                    return [];

                let v = [...this.strings.split(",").map(f => f.trim().toUpperCase())];
                if (this.horizontal)
                    return v.reverse();

                return v;
            },
            convertedMarks() {
                return [...this.marks.split(",").map(f => parseInt(f.trim()))];
            }
        },
        methods: {
            toggleMenu() {
                this.menuVisible = !this.menuVisible
            },
            setGuitar() {
                this.strings = "E, A, D, G, B, E";
                this.length = 21;
            },
            setBass() {
                this.strings = "E, A, D, G";
                this.length = 21;
            },
            set5sBass() {
                this.strings = "B, E, A, D, G";
                this.length = 21;
            },
            set5sBassAlt() {
              this.strings = "E, A, D, G, C";
              this.length = 21;
            },
            setUkulele() {
                this.strings = "G, C, E, A";
                this.length = 12;
            },
            setMandolin() {
                this.strings = "G, G, D, D, A, A, E, E";
                this.length = 20;
            }
        }
    }
</script>

<style lang="scss">

    @import "assets/global";

    #app {

        .md-drawer {
            width: 230px;
            max-width: calc(100vw - 125px);
        }

        .neck-container {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .neck {
            display: flex;
            flex-direction: column;
        }

        &.horizontal {
            .neck-container {
                margin-top: 70px;
            }

            .neck {
                flex-direction: column;
            }

            .string-descriptor {
                border-right: solid 3px darken($fret-color, 3%);
                width: 53px;
                border-left: none;
            }

            .string {
                &.first {
                    .note {
                        border-top: none;
                    }

                    .fret-marker {
                        &:before, &:after {
                            content: "";
                            left: 20px;
                        }

                        &:before, &:after {
                            top: -15px;
                        }

                        &.double-fret {
                            &:before {
                                left: 10px;
                            }

                            &:after {
                                left: 30px;
                            }
                        }
                    }
                }

                &.last .fret-marker {
                    &:before, &:after {
                        content: "";
                        left: 20px;
                    }

                    &:before, &:after {
                        bottom: -15px;
                    }

                    &.double-fret {
                        &:before {
                            left: 10px;
                        }

                        &:after {
                            left: 30px;
                        }
                    }
                }
            }
        }

        &.vertical {
            .neck {
                flex-direction: row;
            }

            .string-descriptor {
                border-bottom: solid 3px darken($fret-color, 3%);
                height: 53px;
                border-top: none;
            }

            .string {
                display: flex;
                flex-direction: column;

                &.first {
                    .note {
                        border-left: none;
                    }

                    .fret-marker {
                        &:before, &:after {
                            content: "";
                            top: 20px;
                        }

                        &:before, &:after {
                            left: -15px;
                        }

                        &.double-fret {
                            &:before {
                                top: 10px;
                            }

                            &:after {
                                top: 30px;
                            }
                        }
                    }
                }

                &.last .fret-marker {
                    &:before, &:after {
                        content: "";
                        top: 20px;
                    }

                    &:before, &:after {
                        right: -15px;
                    }

                    &.double-fret {
                        &:before {
                            top: 10px;
                        }

                        &:after {
                            top: 30px;
                        }
                    }
                }
            }
        }
    }
</style>